.header {
  overflow: hidden;
  position: relative;
  width: 100%;
  z-index: 3;

  &__wrapper {
    padding: 0px 0 15px;
    display: flex;
    flex-direction: column;
    
  }

  &__top {
    position: relative;
    display: flex;
    justify-content: space-between;
    z-index: 1;
    padding: 12px 0;
    gap: 10px;
    @include vp-1023 {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__top::after {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    right: 0;
    background: rgba(23, 23, 23, 1);
    width: 200vw;
    height: 100%;
    z-index: -1;
  }

  &__adress {
    font-family: 'VelaSans';
    font-size: 12px;
    line-height: 150%;
    color: rgba(255, 255, 255, 1);
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    gap: 5px;
    margin: 0;
    svg {
      width: 10px;
      height: 10px;
      flex-shrink: 0;
    }
  }

  &__city {
    font-family: 'VelaSans';
    font-size: 12px;
    line-height: 150%;
    color: rgba(153, 153, 153, 1);
    margin: 0 90px 0 auto;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    gap: 5px;
    svg {
      width: 10px;
      height: 13px;
      flex-shrink: 0;
    } 
    @include vp-1023 {
      margin: 0;
      align-items: flex-start;
    }
  }

  &__phone {
    display: flex;
    align-items: center;
    gap: 8px;
    a {
      font-family: 'VelaSans';
      font-size: 12px;
      line-height: 150%;
      color: rgba(255, 255, 255, 1);
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      gap: 5px;

      svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
      }
    }
  }

  &__phone-list {
    position: relative;
    transition: .3s;
    svg {
      transition: .3s;
      width: 8px;
      height: 8px;
      flex-shrink: 0;
    }

    &.is-active {
      .header__arrow {
        transition: .3s;
        transform: rotate(-180deg);
      }
    }
  }

  &__another-phone {
    position: absolute;
    bottom: -40px;
    left: -140px;
    padding: 8px;
    background: rgba(23, 23, 23, 1);
    white-space: nowrap;
    border-radius: 5px;
    opacity: 0;
    visibility: hidden;
    transition: .3s;

    a {
      svg {
        width: 10px;
        height: 10px;
        flex-shrink: 0;
      }

    }
    

    &.is-active {
      opacity: 1;
      visibility: visible;
    }
  }

  &__another-phone::before {
    content: '';
    position: absolute;
    right: 0;
    left: 0;
    width: 100%;
    display: block;
    height: 10px;
    top: -10px;
  }

  &__center {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
    position: relative;
    gap: 10px;

    @include vp-1023 {
      align-items: flex-start;
    }
  }

  &__center::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: -100%;
    width: 200vw;
    height: 1px;
    background: rgba(232, 232, 232, 1);

    @include vp-1023 {
      display: none;
    }
  }

  &__logo-block {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__logo-text {
    font-family: 'VelaSans';
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: rgba(153, 153, 153, 1);
    max-width: 160px;
    width: 100%;
    margin: 0;

    @include vp-1023 {
      display: none;
    }
  }
  
  .header__logo {
    width: 100px;
    height: 50px;
    flex-shrink: 0;

    @include vp-767 {
      grid-column: 1;
      grid-row: 1;
    }

    @media screen and (max-width: 500px) {
      width: 150px;
      height: 70px;
    }
  }

  &__search {
    position: relative;
    max-width: 336px;
    width: 100%;
    margin-right: auto;
    margin-left: 20px;
    input {
      max-width: 336px;
      width: 100%;
      background: rgba(246, 246, 246, 1);
      border: none;
      border-radius: 10px;
      color: rgba(23, 23, 23, 1);
      padding: 12px 30px 12px 12px;

      @include vp-1023 {
        max-width: 100%;
      }
    }

    input:focus {
      outline: none; 
    }

    input::placeholder {
      font-family: 'VelaSans';
      font-size: 12px;
      line-height: 100%;
      color: rgba(23, 23, 23, 1);
    }

    button {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      padding-right: 11px;
      svg {
        width: 11px;
        height: 11px;
        flex-shrink: 0
      }
    }

    @include vp-1023 {
      max-width: 100%;
      margin: 20px 0;
    }
  }

  &__center-box {
    display: flex;
    gap: 50px;

    @include vp-1023 {
      justify-content: space-between;
      align-items: center;
    }

    @media screen and (max-width: 500px) {
      flex-direction: column;
      gap: 25px;
    }
  }

  &__options {
    display: flex;
    position: relative;
    gap: 16px;
  }

  &__options::after {
    content: '';
    position: absolute;
    right: -35px;
    top: 10px;
    width: 19px;
    height: 1px;
    background-color: rgba(232, 232, 232, 1);
    transform: rotate(90deg);

    @include vp-1023 {
      display: none;
    }
  }

  &__option {
    font-family: 'VelaSans';
    font-size: 12px;
    line-height: 150%;
    color: rgba(23, 23, 23, 1);
  }

  &__icons {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__bottom {
    width: 100%;
    padding: 14px 0;
    transition: .3s;
    @include vp-1023 {
      transform: translateX(100%);
      position: fixed;
      z-index: 10;
      overflow-y: auto;
      overflow-x: hidden;
      top: 90px;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(255, 255, 255, 1);
      padding: 30px 20px;
    }
  }

  &.is-open .header__bottom {
    transform: translateX(0);
  }

  .main-nav__list {
    display: flex;
    justify-content: space-between;
    gap: 15px;
    @include vp-1023 {
      flex-direction: column;
      margin-bottom: 20px;
    }
    
  }

  .main-nav__link {
    font-family: 'VelaSans';
    font-size: 14px;
    line-height: 24px;
    font-weight: 600;
    color: rgba(34, 34, 34, 1);
    transition: .3s;

    &:hover {
      opacity: 0.6;
    }

    @media screen and (max-width: 1160px) {
      font-size: 13px;
    }
  }

  .main-nav__item:last-child .main-nav__link {
    color: rgba(62, 209, 128, 1);
  }

  .main-nav__toggle {
    position: relative;
    display: none;
    width: 50px;
    height: 40px;
    padding: 0;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    // -webkit-transition: opacity rgb(255, 255, 255);
    // -o-transition: opacity rgb(255, 255, 255);
    // transition: opacity rgb(255, 255, 255);
    @include vp-1023 {
      display: flex;
    }
  }
  
  .main-nav__toggle span {
    display: block;
    position: absolute;
    left: 10px;
    height: 5px;
    width: 35px;
    border-radius: 100px;
    background-color: rgba(23, 23, 23, 1);
    -webkit-transition: opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
  }
  
  .main-nav__toggle span:nth-child(1) {
    top: 8px;
  }
  
  .main-nav__toggle span:nth-child(2) {
    top: 50%;
  
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  
  .main-nav__toggle span:nth-child(3) {
    bottom: 8px;
  }
  
  .main-nav__toggle.focus-visible:focus {
    opacity: 0.8;
  }

  &.is-open .main-nav__toggle span:nth-child(1) {
    top: 12px;
    // -webkit-transform: rotate(-47deg) translate(-5px, 11px);
    // -ms-transform: rotate(-45deg) translate(0, -50%);
    transform: rotate(-45deg) translate(0px, 5px);
    background: rgba(23, 23, 23, 1);
    height: 5px;
    width: 35px;
  }
  
  &.is-open .main-nav__toggle span:nth-child(2) {
    opacity: 0;
    visibility: hidden;
  }
  
  &.is-open .main-nav__toggle span:nth-child(3) {
    top: 20px;
    -webkit-transform: rotate(45deg) translate(0, 50%);
    -ms-transform: rotate(45deg) translate(0, 50%);
    transform: rotate(45deg) translate(0px, -7px);
    background: rgba(23, 23, 23, 1);
    height: 5px;
    width: 35px;
  }

}